// angular
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';

// libs
import { NgxDnDModule } from '@swimlane/ngx-dnd';
import { FONT_PICKER_CONFIG, FontPickerConfigInterface, FontPickerModule } from 'ngx-font-picker';
const DEFAULT_FONT_PICKER_CONFIG: FontPickerConfigInterface = {
	apiKey: 'AIzaSyAPcvNvidnjQL-a_2xW2QYox3hT7DQBWyo'
};
// framework
import { SharedModule } from '../../framework/shared/shared.module';

// routes & components
import { FORM_ROUTES } from './form.routes';
import { FormComponent, PreviewDialogComponent } from './form.component';
import { BuildConfigModule } from './components/build/build.config.module';
import { RenderFormModule } from './components/render/render.module';
import { FormStyleComponent } from './components/style/style.component';
import { ConfigComponent } from './components/config/config.component';

@NgModule({
	imports: [
		RouterModule.forChild(FORM_ROUTES),
		CommonModule,
		SharedModule,
		NgxDnDModule,
		FontPickerModule,
		BuildConfigModule.forRoot(),
		RenderFormModule.forRoot(),
	],
	providers: [
		{ provide: FONT_PICKER_CONFIG, useValue: DEFAULT_FONT_PICKER_CONFIG }
	],
	declarations: [
		FormComponent,
		FormStyleComponent,
		PreviewDialogComponent,
		ConfigComponent
	],
	entryComponents: [PreviewDialogComponent]
})
export class FormModule {
}



