import * as _ from 'lodash';
import { AfterContentInit, Component, Input } from '@angular/core';
import { MatSnackBar } from '@angular/material';

import { IFormBuilderEleTypes } from '../../../../../../models';

@Component({
	selector: 'socket-select-component',
	templateUrl: './select.component.html',
})
export class SelectComponent implements AfterContentInit {

	@Input() public item: IFormBuilderEleTypes;
	public isMultiCase: boolean = false;

	constructor(private snackBar: MatSnackBar) {
		//
	}

	public ngAfterContentInit() {
		this.isMultiCase = (this.item.inputType === 'multiple') ? true : false;
	}

	public addOption() {
		if (!_.isEmpty(_.last(this.item.additional.choices))) {
			this.item.additional.choices.push('');
		} else {
			this.snackBar.open('Please fill Field', 'Close', {
				duration: 2000
			});
		}
	}

	public removeOption(idx: number) {
		this.item.additional.choices.splice(idx, 1);
		console.log(this.item.additional.choices);
	}

	public trackByIndex(index: number, value: number) {
		return index;
	}
}



