import { Component, Input, OnInit } from '@angular/core';
import { IFormBuilderEleTypes } from '../../../../../../models';
import { DATE_SELECTING_OPTIONS } from '../../../../../../app.constant';

@Component({
	selector: 'socket-date-component',
	templateUrl: './date.component.html',
	styles: [`
    .divide-equal-child-wrap .mat-form-field{
      width: 42%;
      display: inline-block;
    }
    .divide-equal-child-wrap .mat-form-field:first-child{
      margin-right:5%
    }
  `]
})
export class DateComponent implements OnInit {
	@Input() public item: IFormBuilderEleTypes;
	public dateOptions = DATE_SELECTING_OPTIONS;
	public toggleFields: boolean = false;

	constructor() {
		//
	}

	public ngOnInit() {
		if (this.item.additional.dateFormat) {
			this.toggleFields = true;
		}
	}

}



