import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { ReplaySubject } from 'rxjs/ReplaySubject';
import { IWelcome, WelcomeDetails } from '../../../../../../models';
import * as _ from 'lodash';
import { DomSanitizer } from '@angular/platform-browser';

// <render-form [data]="data"></render-form>
@Component({
	selector: 'socket-render-form-shared-element',
	templateUrl: './shared.render.component.html',
	styles: ['']
})

export class SharedRenderEleComponent implements OnInit, OnDestroy {

	@Input() public data: IWelcome[];
	public mediaUrl: any;
	public dataFound: boolean = false;
	private destroyed$: ReplaySubject<boolean> = new ReplaySubject(1);

	constructor(private _sanitizer: DomSanitizer) {}

	public ngOnInit() {
		if (this.data && this.data.length > 0) {
			this.dataFound = true;
			this.data.forEach((item: IWelcome)=> {
				if (item.isEnabled) {
					// sanitize image url or video URL
					const mediaObj: WelcomeDetails = _.find(item.details, o => o.id === 'media');
					if (mediaObj && mediaObj.isEnabled) {
						if (mediaObj.details.value) {
							this.mediaUrl = this._sanitizer.bypassSecurityTrustResourceUrl(mediaObj.details.value);
						}
					}
				}
			});
		} else {
			this.dataFound = false;
		}
	}
	public ngOnDestroy() {
		this.destroyed$.next(true);
		this.destroyed$.complete();
	}
}



