import { Component, Input, OnInit } from '@angular/core';
import { IFormBuilderEleTypes } from '../../../../../../models';
import * as _ from 'lodash';

@Component({
	// tslint:disable-next-line:component-selector
	selector: 'opinion-render-component',
	templateUrl: './opinion.component.html',
	styleUrls: ['./opinion.component.scss']
})
export class OpinionRenderComponent implements OnInit {

	@Input() public item: IFormBuilderEleTypes;
	public opinion: any[];
	public toggleFields: boolean = false;
	public toggleOpinionLabel: boolean = false;
	public selectedItem: number;

	constructor() {
		//
	}
	public ngOnInit() {
		let start: number = (this.item.additional.opinionStartAt1) ? 1 : 0;
		try {
			this.opinion = _.range(start, Number(this.item.additional.steps));
		} catch (err) {
			this.opinion = _.range(start, 11);
		}
		this.toggleFields = true;
		// check for opinionLabel
		if (this.item.additional && this.item.additional.opinionLabel) {
			this.toggleOpinionLabel = true;
		}
	}

	public setCurrentStep(step: number) {
		this.selectedItem = step;
	}
}



