// angular
import { ModuleWithProviders, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, PatternValidator } from '@angular/forms';
import { FlexLayoutModule } from '@angular/flex-layout';

// libs
import { SharedModule } from '../../../../framework/shared/shared.module';
import { RenderFormComponent } from './render.component';
import { OpinionRenderComponent } from './components/opinion/opinion.component';
import { SharedRenderEleComponent } from './components/shared-opt/shared.render.component';

@NgModule({
	declarations: [
		RenderFormComponent,
		OpinionRenderComponent,
		SharedRenderEleComponent
	],
	exports: [
		RenderFormComponent
	],
	providers: [],
	imports: [
		CommonModule,
		SharedModule.forRoot()
	],
	entryComponents: []
})
export class RenderFormModule {
	public static forRoot(): ModuleWithProviders {
		return {
			ngModule: RenderFormModule,
			providers: []
		};
	}
}



