import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';

import { Font } from 'ngx-font-picker';
import { ReplaySubject } from 'rxjs/ReplaySubject';
import { ColorsSettingsClass, FormDefinationClass, IDesignTools } from '../../../../models/form-builder';
import { DESIGN_OPTIONS } from '../../../../app.constant';

@Component({
	selector: 'socket-form-style',
	templateUrl: './style.component.html',
	styleUrls: ['./style.component.scss']
})

export class FormStyleComponent implements OnInit {

	@Input() public data: FormDefinationClass;
	public designToolsList: IDesignTools[] = DESIGN_OPTIONS;
	public selectedItem: IDesignTools;
	public designSettingForm: FormGroup;
	public presetFonts = ['Arial', 'Serif', 'Helvetica', 'Sans-Serif', 'Open Sans', 'Roboto Slab'];

	private destroyed$: ReplaySubject<boolean> = new ReplaySubject(1);

	constructor(private _fb: FormBuilder) {
		//
	}

	public ngOnInit() {
		this.selectedItem = this.designToolsList[0];

		// init form
		this.initSettingForm();

		this.designSettingForm.valueChanges.takeUntil(this.destroyed$).subscribe(data => {
			if (data) {
				this.data.settings.colors = this.designSettingForm.value;
			}
		});
	}

	public initSettingForm() {
		let obj;
		if (this.data.settings.colors) {
			const c: ColorsSettingsClass = this.data.settings.colors;
			obj = this._fb.group({
				question: [c.question],
				answer: [c.answer],
				background: [c.background],
				button: [c.button]
			});
		} else {
			obj = this._fb.group({
				question: ['#444444'],
				answer: ['#444444'],
				background: ['#fefdfe'],
				button: ['#4caf50']
			});
		}
		this.designSettingForm = obj;
	}

	public onSelectItem(item: IDesignTools) {
		this.selectedItem = item;
	}

	public onChangeFont(e: any) {
		console.log(this.data);
	}

}



