import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormDefinationClass } from '../../../../models';
import { ReplaySubject } from 'rxjs/ReplaySubject';

@Component({
	selector: 'socket-render-form',
	templateUrl: './render.component.html',
	styleUrls: ['./render.component.scss']
})

export class RenderFormComponent implements OnInit, OnDestroy {

	@Input() public data: FormDefinationClass;
	public isVisibleWelcomeScreen: boolean = false;
	private destroyed$: ReplaySubject<boolean> = new ReplaySubject(1);

	constructor() {
		//
	}

	public ngOnInit() {
		//
	}

	public ngOnDestroy() {
		this.destroyed$.next(true);
		this.destroyed$.complete();
	}

}



