// angular
import { ModuleWithProviders, NgModule } from '@angular/core';
import { FormsModule, PatternValidator } from '@angular/forms';
import { CommonModule } from '@angular/common';

// project
import { BuildConfigComponent } from './build.config.component';
import { SelectComponent } from './components/select/select.component';
import { DateComponent } from './components/date/date.component';
import { WelcomeScreenComponent } from './components/welcome-screen/welcome.screen.component';
import { SharedModule } from '../../../../framework/shared/shared.module';

@NgModule({
	declarations: [
		BuildConfigComponent,
		SelectComponent,
		DateComponent,
		WelcomeScreenComponent
	],
	exports: [
		BuildConfigComponent,
		SelectComponent,
		DateComponent,
		WelcomeScreenComponent
	],
	providers: [],
	imports: [
		CommonModule,
		SharedModule.forRoot()
	],
	entryComponents: []
})
export class BuildConfigModule {
	public static forRoot(): ModuleWithProviders {
		return {
			ngModule: BuildConfigModule,
			providers: []
		};
	}
}



