import { Component, Input, OnInit } from '@angular/core';
import { IWelcome, WelcomeDetails } from '../../../../../../models';
import * as _ from 'lodash';

@Component({
	selector: 'socket-welcome-screen-component',
	templateUrl: './welcome.screen.component.html',
	styleUrls: ['./welcome.screen.component.scss']
})
export class WelcomeScreenComponent implements OnInit {

	@Input() public item: IWelcome;

	public options: any[];
	public mediaOptions = [{ value: 'image', label: 'Image' }, { value: 'video', label: 'Video' }];

	constructor() {
		//
	}

	public ngOnInit() {
		console.log(this.item);
	}

	public checkForBtnObj(item: any): boolean {
		return (item.text === 'Button') ? true : false;
	}

}



