import { AfterContentInit, Component, Input, OnInit } from '@angular/core';
import { PatternValidator } from '@angular/forms';
import * as _ from 'lodash';

// libs
import { IFormBuilderEleTypes } from '../../../../models/';
import { T_SCREEN, W_SCREEN } from 'app/app.constant';

@Component({
	selector: 'socket-build-configure',
	templateUrl: './build.config.component.html',
	styles: [`
    :host{
      padding-right:20px;
      display:block
    }
    .form-setting-pane .mat-form-field{
      display: block;
      width:100%
    }
    .mat-form-field-wrapper{
      padding-top:15px;
    }
    .divide-equal-child-wrap .mat-form-field{
      width: 35%;
      display: inline-block;
    }
    .divide-equal-child-wrap .mat-form-field:first-child{
      margin-right:10%
    }
    .liq-mat-slider-wrap .mat-slider-horizontal{
      width: 100%;
    }
  `]
})
export class BuildConfigComponent implements OnInit, AfterContentInit {
	// any because welcome screen and form builder have diff data models
	@Input() public item: any;
	public showAdditionalItem: boolean = false;
	public showSecondaryItem: boolean = false;
	public wscreen: string = W_SCREEN;
	public tscreen: string = T_SCREEN;

	constructor() {
		//
	}

	public ngOnInit() {
		//
	}

	public ngAfterContentInit() {
		this.checkForAdditionalItem(this.item);
		this.checkForSecondaryItem(this.item);
	}

	public checkForSecondaryItem(item: IFormBuilderEleTypes): void {
		const idx = _.indexOf(['number', 'email', 'url', 'yesno', 'date', 'select', 'multiple', 'rating', 'opinion'], item.inputType);
		this.showSecondaryItem = (idx !== -1) ? false : true;
	}

	public checkForAdditionalItem(item: IFormBuilderEleTypes): void {
		const idx = _.indexOf(['select', 'number', 'date', 'multiple', 'rating', 'opinion'], item.inputType);
		this.showAdditionalItem = (idx === -1) ? false : true;
	}

}



