// angular
import { Component, Inject, OnDestroy, OnInit, PLATFORM_ID } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import {
	MAT_DIALOG_DATA, MatDialog, MatDialogRef, MatSnackBar
} from '@angular/material';

// libs
import * as _ from 'lodash';
import { ReplaySubject } from 'rxjs/ReplaySubject';
import 'rxjs/add/operator/takeUntil';
import 'rxjs/add/operator/take';
import 'rxjs/add/observable/of';

// app
import { Observable } from 'rxjs/Observable';
import { AppState } from '../../store';
import { FORM_BUILDER_ELEMENT_INPUT_TYPES, T_SCREEN, W_SCREEN, CONFIGURATION } from '../../app.constant';
import { FormsAPIService } from '../../services/forms/forms.api.service';
import { Store } from '@ngrx/store';
import {
	BaseResponse, FormDefinationClass,
	FormRequestClass, IFormBuilderEleTypes,
	IFormRootRequest, IFormsResponse,
	IThankYou, IWelcome
} from '../../models';
import { routeAnimation } from '../../app.animations';
import { setTimeout } from 'timers';
import { FormsActions } from '../../actions';
import { ToastrService } from 'ngx-toastr';
import { ITeam, IUserDetails } from '../../models/user';
import { UsherClass } from '../../models';

const FORM_BUILDER_OPTIONS: IFormBuilderEleTypes[] = [
	{ name: 'Short text', inputType: FORM_BUILDER_ELEMENT_INPUT_TYPES[1], question: 'What is your name?', required: false },
	{ name: 'Long text', inputType: FORM_BUILDER_ELEMENT_INPUT_TYPES[2], question: 'Write about you.', required: false },
	{ name: 'Email', inputType: FORM_BUILDER_ELEMENT_INPUT_TYPES[3], question: 'Email', required: false },
	{ name: 'Number', inputType: FORM_BUILDER_ELEMENT_INPUT_TYPES[7], question: 'Number', required: false,
	additional: { range: { min: 0, max: 255 } }
	},
	{ name: 'Date', inputType: FORM_BUILDER_ELEMENT_INPUT_TYPES[6], question: 'Enter date', required: false,
	additional: { dateFormat: { structure: 'DDMMYYYY', separator: '/' } } },
	{ name: 'Select box', inputType: FORM_BUILDER_ELEMENT_INPUT_TYPES[8], question: 'Choose an option',
	required: false, additional: { choices: ['Option 1'] } },
	{ name: 'Statement', inputType: FORM_BUILDER_ELEMENT_INPUT_TYPES[0], question: 'Statement', required: false },
	{ name: 'Website / URL', inputType: FORM_BUILDER_ELEMENT_INPUT_TYPES[4], question: 'Website', required: false },
	{ name: 'Yes/No', inputType: FORM_BUILDER_ELEMENT_INPUT_TYPES[5], question: 'Yes/No', required: false, additional: { answer: null } },
	{ name: 'Multiple choice', inputType: FORM_BUILDER_ELEMENT_INPUT_TYPES[9], question: 'Multiple choice',
	required: false, additional: { multipleSelection: false, choices: ['Option 1'] } },
	{ name: 'Rating', inputType: FORM_BUILDER_ELEMENT_INPUT_TYPES[10], question: 'Rating', required: false, additional: { steps: 5 } },
	{ name: 'Opinion Scale', inputType: FORM_BUILDER_ELEMENT_INPUT_TYPES[11], question: 'Opinion Scale',
	required: false, additional: { steps: 5, opinionLabel: { left: '', center: '', right: '' }, opinionStartAt1: false } }
];

@Component({
	templateUrl: './form.component.html',
	styleUrls: ['./form.component.scss'],
	animations: [routeAnimation]
})

export class FormComponent implements OnInit, OnDestroy {
	public wscreen: string = W_SCREEN;
	public tscreen: string = T_SCREEN;
	public formDataLoaded: boolean = false;
	public selectedItem: IFormBuilderEleTypes;
	public itemIsSelected: boolean = false;
	public formBuilderOptions: IFormBuilderEleTypes[] = FORM_BUILDER_OPTIONS;
	public welcomeScreenOptions: IWelcome[];
	public thankScreenOptions: IThankYou[];
	public formConfiguration: FormRequestClass;
	private formsList$: Observable<IFormsResponse[]> = Observable.of([]);
	private selectedForm: IFormsResponse = null;
	private selectedFormId: string = null;
	private activeTeam: ITeam = null;

	// private below
	private destroyed$: ReplaySubject<boolean> = new ReplaySubject(1);

	constructor(
		private route: ActivatedRoute,
		private store: Store<AppState>,
		private _router: Router,
		private _dialog: MatDialog,
		private _matSnackBar: MatSnackBar,
		private _formsActions: FormsActions,
		private _toasty: ToastrService,
		@Inject(PLATFORM_ID) private platformId: string
	) {
		// setup initial data
		this.initWelcomeData();
		this.initThankYouData();
	}

	public ngOnInit() {
		// check if route params exist else redirect to dashboard
		this.route.params.takeUntil(this.destroyed$).subscribe(params => {
			if (params['formId']) {
				if (params['formId'] === 'new') {
					this.selectedFormId = null;
					this.drawUI('new');
				} else {
					this.selectedFormId = _.cloneDeep(params['formId']);
				}
			} else {
				// redirect to dashboard
				this.selectedFormId = null;
				this._router.navigate(['/', 'app', 'home']);
			}
		});

		// all form list
		this.store.select(p => p.form.formList).takeUntil(this.destroyed$).subscribe((data: IFormsResponse[]) => {
			if (data && data.length) {
				this.formsList$ = Observable.of(data);
				// on page refresh case
				if (this.selectedFormId) {
					this.getActiveForm(this.selectedFormId);
				}
			}
		});

		// get active form data
		this.store.select(p => p.form.activeForm).takeUntil(this.destroyed$).subscribe((data: IFormsResponse) => {
			if (data) {
				this.selectedForm = data;
			}
		});

		// get active team data
		this.store.select(p => p.home.selectedTeam).takeUntil(this.destroyed$).subscribe((data: ITeam) => {
			if (data) {
				this.activeTeam = data;
			}
		});

		// watch for team change event if true redirect to home
		this.store.select(p => p.form.teamChangeEventHappening).takeUntil(this.destroyed$).subscribe((event: boolean) => {
			if (event && this.selectedFormId) {
				this._router.navigate(['/', 'app', 'home']);
			}
		});
	}

	public getActiveForm(id: string) {
		this.formsList$.take(1).subscribe((data: IFormsResponse[]) => {
			if (data && data.length) {
				let res = _.find(data, item => item.id === id);
				this.selectedForm = (res) ? res : data[0];
				this.selectedFormId = _.cloneDeep(data[0].id);
				this.drawUI('old');
				this._router.navigate(['/', 'app', 'form', data[0].id]);
			}
		});
	}

	public drawUI(type: string) {
		this.formConfiguration = (this.selectedForm && type === 'old') ? new FormRequestClass(this.selectedForm) : new FormRequestClass();
		if (!this.formConfiguration.definition || _.isNull(this.formConfiguration.definition)) {
			this.formConfiguration = { ...this.formConfiguration, definition: new FormDefinationClass() };
		}
		console.log('final object:', this.formConfiguration);
		this.formDataLoaded = true;
	}

	public builderDrag(e: any) {
		const item = e.value;
		// tslint:disable-next-line:no-bitwise
		item.data = item.inputType === 'number' ? (Math.random() * 100) | 0 : Math.random().toString(36).substring(20);
	}

	public log(e: any) {
		console.log(e.type, e);
	}

	public onSelectItem(item: any) {
		this.itemIsSelected = false;
		this.selectedItem = item;
		setTimeout(() => {
			this.itemIsSelected = true;
		}, 100);
	}

	/**
	 * if form is old then open new tab
	 */
	public previewForm() {
		if (this.selectedFormId && this.selectedForm && isPlatformBrowser(this.platformId)) {
			const URL = `${CONFIGURATION.ApiUrl}forms/${this.selectedFormId}.html`;
			window.open(URL);
		} else {
			if (this.formConfiguration.definition.formData.length === 0) {
				this._matSnackBar.open('Please Add Some Elements first', 'Close', {
					duration: 2000
				});
				return false;
			}
			let dialogRef = this._dialog.open(PreviewDialogComponent, {
				width: '60%',
				data: this.formConfiguration.definition
			});

			dialogRef.afterClosed().subscribe(result => {
				// console.log('The dialog was closed');
			});
		}
	}

	public saveForm() {
		if (!this.activeTeam) {
			this._toasty.warning('Team not found Go back and select team', 'Superform');
			return false;
		}
		let model: IFormRootRequest = {
			form: this.formConfiguration
		};
		this.store.dispatch(this._formsActions.createForm(this.activeTeam.id, model));
	}

	public updateForm() {
		if (!this.selectedForm.id) {
			this._toasty.warning('Something went wrong, Please relaod page', 'Superform');
			return false;
		}
		let model: IFormRootRequest = {
			form: this.formConfiguration
		};
		this.store.dispatch(this._formsActions.updateForm(this.selectedForm.id, model));
	}

	public deleteItem(item: any) {
		// reset selected item
		this.itemIsSelected = false;
		this.selectedItem = null;
		// remove item
		_.remove(this.formConfiguration.definition.formData, arg => {
			return arg === item;
		});
	}

	public initWelcomeData() {
		const obj: any = {
			name: 'Welcome Screen',
			inputType: W_SCREEN,
			isEnabled: true,
			details: []
		};
		this.welcomeScreenOptions = [];
		this.welcomeScreenOptions.push(new UsherClass(obj));
	}

	public initThankYouData() {
		const obj: any = {
			name: 'Thank You Screen',
			inputType: T_SCREEN,
			isEnabled: true,
			details: []
		};
		this.thankScreenOptions = [];
		this.thankScreenOptions.push(new UsherClass(obj));
	}

	public deleteWScreenItem(type: string, item: any) {
		// reset selected item
		this.itemIsSelected = false;
		this.selectedItem = null;
		// remove item
		this.formConfiguration.definition[type] = [];
	}

	public dropOccurredOnUsherBox(type: string, e?: any) {
		console.log(e.type, e);
		// check for welcome and thanks scenerio
		if (e.type === 'drop' && (_.indexOf([this.tscreen, this.wscreen], type) !== -1)) {
			this.itemIsSelected = false;
			this.selectedItem = e.value;
			setTimeout(() => {
				this.itemIsSelected = true;
			}, 100);
		}
	}

	public ngOnDestroy() {
		this.destroyed$.next(true);
		this.destroyed$.complete();
	}
}

@Component({
	selector: 'socket-preview-dialog',
	template: `<h2 mat-dialog-title>Preview Form</h2>
  <mat-dialog-content><socket-render-form [data]="renderData"></socket-render-form></mat-dialog-content>`,
})
export class PreviewDialogComponent {
	constructor(public dialogRef: MatDialogRef<PreviewDialogComponent>, @Inject(MAT_DIALOG_DATA) public renderData: any) { }
}



